@import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Oswald:wght@500&family=Pathway+Gothic+One&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Kanit&display=swap");

/*main container for cards*/

.title {
  color: white;
  font-size: 25px;
  font-family: "Roboto", sans-serif;
  text-align: center;
  text-decoration: solid;
  font-family: "Kanit", sans-serif;

  opacity: 0.75;
}
.container {
  /*want to move cards to next line and maintain center alignment on resizing*/
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  text-decoration: solid;
}

/*each card is exchange info*/
.card {
  /* glass panel look*/
  background: rgba(255, 255, 255, 0.4);
  border-radius: 18px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.9);
  backdrop-filter: blur(6px);
  -webkit-backdrop-filter: blur(5px);
  border: 1px solid rgba(255, 255, 255, 0.3);

  /*sizing*/
  width: 300px;
  height: 300px;

  /*content positioning*/
  justify-content: space-between;
  align-content: center;
  margin: 50px;
  padding-top: 20px;
  flex-wrap: wrap;

  /*transition timing setting*/
  transition: all 1s;
}

/* show pointer hand on hover*/
.card:hover {
  cursor: pointer;
}

/* CARD CHILD DIVS */
.card h1 {
  padding-bottom: 2px;
  margin: 1px;
  color: #ffffff;
  opacity: 0.9;
  font-size: 1.5rem;
  margin-bottom: 25px;
}

.card p {
  padding-bottom: 2px;
  margin: 1px;
  color: #ffffff;
  opacity: 0.9;
  font-size: 0.9rem;
  margin-bottom: 10px;
  padding-left: 20px;
  padding-right: 20px;
  word-wrap: break-word;
}

.card img {
  margin-top: 10px;
  border-radius: 20px;
  transition: 0.7s ease-in-out;
}

.card h2 {
  /*setting opacity to zero to make invisible*/
  opacity: 0;
  margin-top: 23px;
  border: solid 2px white;
  background-color: white;
  color: gray;
  font-size: 20px;
  /*visibility: hidden;*/
  transition: 1.1s;
}

/*CHILD DIVS HOVER PROPERTIES*/

.card:hover h2 {
  /*slowly increase opacity to make text appear*/
  opacity: 0.5;
  transition: opacity 1.1s;
}

.card:hover img {
  /*rotate image, raise image, add shadow to show floating effect*/
  transform: translateY(-24px) rotate(360deg);

  box-shadow: 0 8px 2px 4px #414a4c;

  transition: box-shadow 1.9s transform 0.7s;
}
